.form {
    display: flex;
    flex-direction: column;
}
.container{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 600px;
    min-height: 400px;
}

.inputContainer {
    display: flex;
    align-items: center;
    width: 500px;
    margin: 10px;
    flex-direction: column;
}
.input {
    width: 300px;
}

.textarea {
    width: 300px;
}
@media screen and (max-width: 380px) {
    .input {
        width: 200px;
    }
    .textarea {
        width: 200px;
    }
}

